@import url(https://fonts.googleapis.com/css2?family=Acme&display=swap);
.contenedorGeneralLogin{height:100%;width:100%}.contenedorGeneralLogin #myVideo{position:fixed;right:0;bottom:0;min-width:100%;min-height:100%}.contenedorGeneralLogin .content{position:fixed;bottom:0;background:rgba(0,0,0,0.5);color:#f1f1f1;width:100%;padding:20px}.contenedorGeneralLogin #myBtn{width:200px;font-size:18px;padding:10px;border:none;background:#000;color:#fff;cursor:pointer}.contenedorGeneralLogin #myBtn:hover{background:#ddd;color:black}.contenedorGeneralLogin .login{overflow:hidden;background-color:#b6afaf;padding:40px 30px 30px 30px;border-radius:10px;position:absolute;top:50%;left:50%;width:400px;transform:translate(-50%, -50%);transition:transform 300ms, box-shadow 300ms;box-shadow:5px 10px 10px rgba(2,128,144,0.2)}.contenedorGeneralLogin .login::before,.contenedorGeneralLogin .login::after{content:'';position:absolute;width:600px;height:600px;border-top-left-radius:40%;border-top-right-radius:45%;border-bottom-left-radius:35%;border-bottom-right-radius:40%;z-index:-1}.contenedorGeneralLogin .login::before{background-color:rgba(228,129,68,0.84);-webkit-animation:wawes 6s infinite linear;animation:wawes 6s infinite linear}.contenedorGeneralLogin .login::after{left:35%;bottom:-125%;background-color:rgba(11,60,157,0.82);-webkit-animation:wawes 7s infinite;animation:wawes 7s infinite}.contenedorGeneralLogin .login>.user,.contenedorGeneralLogin .login .password,.contenedorGeneralLogin .login .vacio{font-family:"Asap",sans-serif;display:block;border-radius:5px;font-size:16px;background-color:white;width:100%;border:0;padding:10px 10px;margin:15px -10px;transition:background-color 1.2s ease}.contenedorGeneralLogin .login .user:focus{background-color:rgba(182,181,181,0.335)}.contenedorGeneralLogin .login .password:focus{background-color:rgba(182,181,181,0.335)}.contenedorGeneralLogin .login .recuperar{display:flex;justify-content:flex-end;margin-top:10px;float:left}.contenedorGeneralLogin .login .recuperar .recuperarPass{font-size:12px;font-family:'Fredoka One', cursive;color:#070000}.contenedorGeneralLogin .login .recuperar .recuperarPass:hover{cursor:pointer;text-decoration:underline}.contenedorGeneralLogin .login>.btnLogin{font-family:"Asap",sans-serif;cursor:pointer;color:#fff;font-size:12px;text-transform:uppercase;width:90px;border:0;padding:12px 15px;float:right;margin-top:10px;margin-left:-5px;border-radius:5px;background-color:#f45b69;transition:background-color 300ms;text-decoration:none}.contenedorGeneralLogin .login>.btnLogin:hover{background-color:#f24353}.contenedorGeneralLogin .login .tituloLogin{color:rgba(16,19,6,0.84);font-family:'Acme', sans-serif;font-size:20px;text-align:center}.contenedorGeneralLogin .login .subTituloLogin{font-family:'Fredoka One', cursive;font-size:14px;text-align:center}.contenedorGeneralLogin .obtenerCuenta{color:#6a82fb}.contenedorGeneralLogin .obtenerCuenta:hover{cursor:pointer;text-decoration:underline}@-webkit-keyframes wawes{from{transform:rotate(0)}to{transform:rotate(360deg)}}@keyframes wawes{from{transform:rotate(0)}to{transform:rotate(360deg)}}

